body{
    margin: 0px;
}
#hero{
    background-image: url('./assests/pink.jpg');
    background-size: cover;
    background-position: center center;
    position: relative;
    top: 0%;
    bottom: 0%;
    height: 100vh;
    z-index: -10;
}
#hero-overlay{
    position: absolute;
    top: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
    z-index: -5;
    background-color: (o,o,o,0.5)
}
#words{
    margin-top: 15%;
    color:white;
    font-size:150%;
}

#school{
    color: maroon;
} 
#spook{
    font-size: small;
}
.anikait{
    color:mistyrose;
    /* 47FB71 */
}
#major{
    color: mediumslateblue;
}
@media screen and (max-width: 600px) {
    #comp-words{
      font-size: 15px;
    }
    #title{
        font-size: 20px;
    }
    #life{
        visibility: hidden;
        display: none;
    }
    #spook{
        font-size: 8px;
    }
    #major{
        color:mistyrose;
    }
}